import { getLogList } from "@/api/modules/web/log";
import { Card, message } from "antd";
import Table, { ColumnsType } from "antd/es/table";
import { useEffect, useState } from "react";
import { useSearchParams } from "react-router-dom";


interface DataType {
    key: React.Key;
    level: string;
    content: string;
    metrics: string;
    categories: string;
}

const LogList = () => {
    
    const [pageNum, setPageNum] = useState(1);
    const [pageSize, setPageSize] = useState(10);
    const [logTotal, setLogTotal] = useState(0);
    const [logList, setLogList] = useState<DataType[]>([]);
    const [selectedRowKeys, setSelectedRowKeys] = useState<React.Key[]>([]);
    const [searchParams] = useSearchParams();
    const [messageApi, contextHolder] = message.useMessage();

    const columns: ColumnsType<DataType> = [
        {
            title: '日志级别',
            dataIndex: 'level',
            width: 100,
        },
        {
            title: '日志内容',
            dataIndex: 'content',
            width: 200,
        },
        {
            title: '自定义数值',
            dataIndex: 'metrics',
            width: 200,
        },
        {
            title: '事件本身的维度',
            dataIndex: 'categories',
            width: 200,
        },
    ];

    const onSelectChange = (newSelectedRowKeys: React.Key[]) => {
        setSelectedRowKeys(newSelectedRowKeys);
    };

    const rowSelection = {
        selectedRowKeys,
        onChange: onSelectChange,
    };

    const initLogList = async () => {
        const appId = searchParams.get('appId');
        if (!appId) {
            messageApi.open({
                type: 'error',
                content: 'appId不能为空!',
            });
            return;
        }
        const result = await getLogList({ pageNum, pageSize,appId });
        if (result.data) {
            setLogTotal(result.data.total);
            const data: DataType[] = [];
            result.data.data.forEach(item => {
                data.push({
                    key: item.logId,
                    level: item.level,
                    content: item.content,
                    metrics: item.metrics,
                    categories: item.categories
                })
            });
            setLogList(data);
        }
    };

    useEffect(() => {
        initLogList();
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [])

    useEffect(() => {
        initLogList();
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [pageNum, pageSize])

    const pagination = {
        showSizeChanger: true,
        onChange: (page: number, pageSize: number) => {
            setPageNum(page);
            setPageSize(pageSize);
        },
        defaultCurrent: pageNum,
        total: logTotal
    }

    return (<Card title="日志列表">{contextHolder}<Table style={{ marginTop: '12px' }} rowSelection={rowSelection} pagination={pagination} columns={columns} dataSource={logList} /></Card>);
}

export default LogList;